* {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
}

body,
button,
a {
   margin: 0;
   padding: 0;
   font-family: Montserrat, sans-serif;
   font-weight: 400;
   line-height: 20px;
   font-size: 16px;
   outline: none;
   border: none;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
   box-sizing: border-box;
}

code {
   font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
}
